/* 전체 스크롤바에 대한 스타일 */
::-webkit-scrollbar {
  width: 8px; /* 스크롤바 너비 */
}

/* 스크롤바 트랙(배경) */
::-webkit-scrollbar-track {
  background: transparent; /* 배경을 투명하게 */
}

/* 스크롤바 핸들(움직이는 부분) */
::-webkit-scrollbar-thumb {
  background: gray; /* 스크롤바 핸들의 색상 */
  border-radius: 10px; /* 핸들의 모서리를 둥글게 */
}

/* 스크롤바를 마우스로 클릭할 때 */
::-webkit-scrollbar-thumb:active {
  background: darkgray;
}

.App {
  text-align: center;
}

.App-header {
  width: 100vw;
  height: 100vh;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
} 

.hikingMap {
  width: 100vw;
  height: 100vh;
}

.hikingMap .mapContainer {
  width: 100%;
  height: 100%;
}

.hikingMap .version {
  position: absolute;
  bottom: 3%;
  right: 2.5%;
  z-index: 444;
  font-size: 12px;
  color: #ffffff;
}

.hikingMap .info {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 400px;
  height: 100vh;
  z-index: 999;
  border-radius: 0 15px 15px 0;
  background-color: #f1f1f1;
}

.hikingMap .info.closed {
  transform: translateX(-100%);
  transition: all 0.5s;
}

.hikingMap .info.open {
  transform: translateX(0);
  transition: all 0.5s;
}

.hikingMap .info .infoToggle {
  width: 25px;
  height: 50px;
  position: absolute;
  top: 50px;
  left: 400px;
  background-color: rgb(0, 0, 0);
  border-radius: 0 10px 10px 0;
  z-index: 999;
}

.hikingMap .info .infoToggle > img {
  color: #ffc800;
}

.hikingMap .info .infoToggle:hover {
  background-color: rgb(72, 72, 72);
}

.hikingMap .info .search {
  width: 100%;
  height: 10%;
}

.hikingMap .info .search > input {
  width: 90%;
  height: 40px;
  border-radius: 20px;
  margin-top: 15px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  background-color: #e6e4e4;
  border: 1px solid #c6c6c6;
}

.hikingMap .info .search > ul {
  position: absolute;
  left: 50%;
  width: 90%;
  height: 100%;
  list-style: none;
  padding: 0;
  margin: 10px auto;
  transform: translateX(-50%);
}

.hikingMap .info .search > ul > li {
  margin-bottom: 10px;
}

.hikingMap .info .search > ul > li > button {
  width: 90%;
  height: 50px;
  border-radius: 30px;
  background-color: #e6e4e4;
  border: 1px solid #c6c6c6;
  font-size: 13px;
  text-align: left;
  padding-left: 30px;
  color: rgb(0, 0, 0);
}

.hikingMap .info .search > ul > li > button:hover {
  background-color: rgb(207, 207, 207);
}

.hikingMap .info .search > ul > li > button > strong {
  font-size: 15px;
  color: #000000;
}

.hikingMap .info .tourList {
  width: 100%;
  height: 90%;
}

.hikingMap .info .tourList > .nearSpot {
  width: 100%;
  height: 35px;
  margin: 0 0 5px 30px;
  line-height: 35px;
  font-size: 15px;
  text-align: left;
}

.hikingMap .info .tourList > ul {
  padding: 0;
  height: calc(100% - 35px);
  overflow: auto;
}

.hikingMap .info .tourList > ul > li {
  list-style: none;
  font-size: 13px;
  color: rgb(0, 0, 0);
  margin-top: 20px;
  text-align: left;
  margin-left: 30px;
}

.hikingMap .info .tourList > ul > li:first-child{
  margin-top: 0px;
}

.hikingMap .info .tourList > ul > li > strong {
  font-size: 20px;
  color: #000000;
}





.hikingMap .chatBot {
  position: fixed;
  top: 0px;
  right: 0px;
  width: 400px;
  height: 100vh;
  z-index: 999;
  border-radius: 15px 0 0 15px;
  background-color: #000000d4;
}

.hikingMap .chatBot.closed {
  transform: translateX(100%);
  transition: all 0.5s;
}

.hikingMap .chatBot.open {
  transform: translateX(0);
  transition: all 0.5s;
}

.hikingMap .chatBot .chatBotToggle {
  width: 25px;
  height: 50px;
  position: absolute;
  top: 50px;
  right: 400px;
  background-color: rgb(0, 0, 0);
  border-radius: 5px 0 0 5px;
  z-index: 999;
}

.hikingMap .chatBot .chatBotToggle:hover {
  background-color: rgb(72, 72, 72);
}

.hikingMap .chatBot .chat-container {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: #f1f1f1;
  border-radius: 15px 0 0 15px;
}

.hikingMap .chatBot .chat-container .chat-window {
  width: 100%;
  height: 85%;
  position: absolute;
  top: 0;
  left: 0;
  overflow-y: scroll;
  overflow-x: hidden;
}

.hikingMap .chatBot .chat-container .chat-window .question {
  max-width: 70%;
  width: auto;
  margin: 10px;
  padding: 8px;
  text-align: right;
  float: right;
  border: 1px solid #000000;
  border-radius: 10px;
  font-size: 20px;
  background-color: #e6e4e4;
}

.hikingMap .chatBot .chat-container .chat-window .answer {
  max-width: 80%;
  width: auto;
  margin: 10px 30px;
  padding: 8px;
  text-align: left;
  float: left;
  border: 1px solid #000000;
  border-radius: 10px;
  font-size: 20px;
  background-color: #e6e4e4;
}

.hikingMap .chatBot .chat-container .chat-input {
  position: absolute;
  display: flex;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 15%;
  justify-content: center; /* 수평 중앙 정렬 */
  align-items: center;     /* 수직 중앙 정렬 */
}


.hikingMap .chatBot .chat-container .chat-input textarea {
  width: 70%;
  height: 40px;
  padding: 0 20px;
  line-height: 40px;
  border-radius: 20px 0 0 20px;
  outline: none;
  resize: none;
  font-size: 16px;
  text-align: center;
  background-color: #e6e4e4;
  border: 1px solid #c6c6c6;
}

.hikingMap .chatBot .chat-container .chat-input button {
  all: unset;
  display: block;
  width: 50px;
  height: 40px;
  font-size: 16px;
  color: #FECE00;
  text-align: center;
  border-radius: 0 20px 20px 0;
  background-color: #000000;
}

.hikingMap .chatBot .chat-container .chat-input button:hover {
  background-color: #303030;
}




@media (max-width: 480px){
  .hikingMap .info {
    width: 100%;
    height: 90%;
    top: auto;
    bottom: 0px;
    border-radius: 15px 15px 0 0;
  }

  .hikingMap .info.closed {
    transform: translateY(100%);
    transition: all 0.5s;
  }
  
  .hikingMap .info.open {
    transform: translateY(0);
    transition: all 0.5s;
  }

  .hikingMap .info .infoToggle {
    top: -25px;
    left: 50%;
    width: 50px;
    height: 25px;
    transform: translateX(-50%);
    border-radius: 5px 5px 0 0;
  }

  .hikingMap .info .infoToggle img {
    transform: rotate(-90deg) translateX(12.5px);
  }

  .hikingMap .chatBot {
    width: calc(100% - 30px);
  }

  .hikingMap .chatBot .chatBotToggle {
    left: -25px;
  }
}